import './App.css';

import React, { useState } from 'react';


const App = () => {

  const [cupon, setCupon] = useState('')
  const [error, setError] = useState(false);


  const handleChange = (e) => {
    setCupon(e.target.value.toUpperCase());
  }

  const validate = (e) => {
    e.preventDefault();
    const cupon = "pvr4";

    if (cupon === cupon.toLocaleLowerCase())
      window.location.assign("http://smrtsecure-ad.com/smartlink/?a=134499&sm=8253&mt=28");
    else {
      setCupon('');
      setError(true);
    }
  }

  return (

    <form onSubmit={validate} className="formClass">
      <label>Enter Cupon For More Videos</label>
      <input value={cupon} onChange={handleChange} required type="text" />
      <button type="submit">Submit</button>
      <h1 className="cupon">ENTER CODE: <span className="span">PVR4</span></h1>
    </form>

  );
}

export default App;



